.post_card {
  position: relative;
}
.post_card .thumb {
  filter: brightness(0.7);
  vertical-align: middle;
  min-height: 100px;
}
.post_card .info {
  position: absolute;
  left: 0;
  bottom: 0;
  color: white;
  padding: 10px 10px;
  font-size: 12px;
}
.post_card .info .desc span.dus::before {
  content: "/";
  margin: 0 5px;
}
.post_card .info h5 {
  margin-top: 10px;
  font-size: 14px;
  font-weight: 600;
}
