.App {
  width: 100vw;
  height: 100vh;
  background-color: #1a1a1a;
}
.App .navbar {
  display: none;
}
.App.shownav {
  padding-top: 45px;
}
.App.shownav .navbar {
  display: flex;
}
.App .am-navbar-dark {
  background-color: #1a1a1a;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 999;
}
.navbar .navlis {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar .navlis li {
  padding: 10px 0;
  list-style: none;
}
.navbar .navlis li a {
  padding: 9px 5px;
  margin: 0 10px;
  font-size: 14px;
  font-weight: 600;
  color: white;
}
.navbar .navlis li a.active {
  border-bottom: 3px solid white;
}
.modal_box {
  width: 100vw;
  height: 100vh;
  padding: 65px 30px 30px 30px;
  background-image: url(https://cs.vmovier.com/Uploads/Banner/2021/06/29/60da89afcedd0.jpg@750w_700h_1e_1c_50-20bl);
  background-size: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.modal_box .close {
  position: absolute;
  top: 20px;
  left: 30px;
  font-size: 20px;
  color: white;
}
.modal_box .msg_box {
  border: 1px solid rgba(255, 255, 255, 0.342);
  border-radius: 10px;
  overflow: hidden;
}
