.home_card h3 {
  text-align: center;
  line-height: 4em;
  font-size: 1em;
  color: white;
}
.home_card.special ul {
  display: flex;
  flex-wrap: wrap;
}
.home_card.special ul li {
  width: 50%;
}
.home_card.special ul li .info > h5 {
  width: 10em;
  height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.home_card.special ul li:first-child {
  width: 100%;
}
.home_card.special ul li:first-child .info > h5 {
  width: 100%;
}
