ul.cate {
  display: flex;
  flex-wrap: wrap;
}
ul.cate li.cate_list {
  width: 50vw;
  height: 50vw;
  position: relative;
}
ul.cate li.cate_list img {
  filter: brightness(0.7);
}
ul.cate li.cate_list h5 {
  color: white;
  font-weight: bolder;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 9;
  text-align: center;
  line-height: 50vw;
}
