.search .search_top {
  width: 100vw;
  padding: 25px 10px 0 10px;
  margin-bottom: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.search .search_top .top_left {
  width: 80vw !important;
  background: #252525;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 5px;
}
.search .search_top .top_left input {
  flex: 1;
  background-color: transparent;
  border: none;
  padding-left: 5px;
  color: white;
  z-index: 9;
}
.search .search_top .top_right {
  text-align: center;
  width: 15vw;
}
.search .search_hots {
  padding: 40px 10px 0 10px;
}
.search .search_his {
  padding: 40px 10px 0 10px;
}
.search .search_his .search_hot {
  display: flex;
  flex-wrap: wrap;
}
.search .search_his .search_hot span {
  border: 1px solid transparent;
  border-radius: 3px;
  margin: 5px 10px 5px 0;
  padding: 8px;
  font-size: 12px;
  background-color: #252525;
  color: white;
}
